import React, { useState, useEffect} from 'react'

// Assets and Styling
import draft from "./../imgs/draft.png"
import "./../css/content.scss";

const Presales = () => {
    const [opacity, setOpacity] = useState(false);
    const [imageOpacity, setImageOpacity] = useState(false);
    const [headerOpacity, setHeaderOpacity] = useState(false);

    useEffect(() => {
        setHeaderOpacity(true);
    }, [headerOpacity]);

    useEffect(() => {
        setOpacity(true);
    }, [opacity]);

    useEffect(() => {
        setImageOpacity(true);
    }, [imageOpacity]);

    return (
        <>
            <h1 className={headerOpacity ? "headerShow" : "textHide"}> Presales</h1>

            <p className={opacity ? "textShow" : "textHide"}> We are currently finishing up illustrations, and we will submit
                our order to publish in mid-May. We expect to receive our books in late May, and we will ship your orders from then to early June!
            </p>

            <img src = {draft} className={imageOpacity ? "show" : "hide"} alt={"Cover of tinker tim"} />  
        </>
    )
}

export default Presales