import React, {useState, useEffect} from 'react'
import "./../css/content.scss";
import contact from "./../imgs/contact.png"

function Contact() {
    const [opacity, setOpacity] = useState(false);
    const [headerOpacity, setHeaderOpacity] = useState(false);
    const [imageOpacity, setImageOpacity] = useState(false);

    useEffect(() => {
        setHeaderOpacity(true);
    }, [headerOpacity]);

    useEffect(() => {
        setOpacity(true);
    }, [opacity]);

    useEffect(() => {
        setImageOpacity(true);
    }, [imageOpacity]);



  return (
    <div>
        <h1 className={headerOpacity ? "headerShow" : "textHide"}>  Contact Us</h1>
        <p className={opacity ? "textShow" : "textHide"}> You can reach us at tinkertimbook@gmail.com </p>
        <br></br>
        <p className={opacity ? "textShow" : "textHide"}>  After an order, you should receive a receipt. Please let us know if you have any issues! </p>
        <img src = {contact} className={imageOpacity ? "show" : "hide"} alt={"Cover of tinker tim"} />  
    
    </div>
  )
}

export default Contact