import React, { useState } from 'react'
import { useEffect } from 'react';

// Assets and Styling
import "./../css/content.scss";
import cover from "./../imgs/cover.png";

const Story = () => {
  const [opacity, setOpacity] = useState(false);
  const [imageOpacity, setImageOpacity] = useState(false);
  const [headerOpacity, setHeaderOpacity] = useState(false);

  useEffect(() => {
    setHeaderOpacity(true);
  }, [headerOpacity]);

  useEffect(() => {
    setOpacity(true);
  }, [opacity]);

  useEffect(() => {
    setImageOpacity(true);
  }, [imageOpacity]);

  return (
    <>
      <h1 className={headerOpacity ? "headerShow" : "textHide"}> The Story </h1>
      <p className={opacity ? "textShow" : "textHide"}>
        Tim is a bear who loves to tinker. Tim flies paper airplanes for his brother and necklaces for his mom but Tim's dad doesn't seem to get  it. He pushes Tim to do something productive instead, like learning an instrument. Tim's mom reminds his dad that he likes to tinker in his own way as well, so at the end of the day, Tim's dad comes around to encourage Timâ€™s natural curiosity.
      </p>
      <img className={imageOpacity ? "show" : "hide"} src={cover} alt={"Cover of tinker tim"} />
      <div className='line-breaks'></div>
    </>
  )
}

export default Story