import React, { useState, useEffect } from 'react'
import draft_cover from "./../imgs/tim_logo_draft.png";

// Assets and Styling
import "./../css/content.scss";

const Reason = () => {
    const [opacity, setOpacity] = useState(false);
    const [imageOpacity, setImageOpacity] = useState(false);
    const [headerOpacity, setHeaderOpacity] = useState(false);
    const [secondHeaderOpacity, setSecondHeaderOpacity] = useState(false);
    const [secondTextOpacity, setSecondTextOpacity] = useState(false);

    useEffect(() => {
        setHeaderOpacity(true);
    }, [headerOpacity]);

    useEffect(() => {
        setOpacity(true);
    }, [opacity]);

    useEffect(() => {
        setImageOpacity(true);
    }, [imageOpacity]);

    useEffect(() => {
        setSecondHeaderOpacity(true);
    }, [secondHeaderOpacity]);

    useEffect(() => {
        setSecondTextOpacity(true);
    }, [secondTextOpacity]);

    return (
        <>
            <h1 className={headerOpacity ? "headerShow" : "textHide"}> The Reason</h1>

            <p className={opacity ? "textShow" : "textHide"}>
                Tinker Tim was inspired by our team's childhood and upbringing. Many of our team members were encouraged to pursue specific activities for success like math, chemistry, or even piano as children.
                Simply exploring our own interest wasn't really a thing. In writing this story, we want to encourage parents and their children to freely explore whatever comes to mind. To have fun, and tinker!
            </p>

            <img src={draft_cover} className={imageOpacity ? "show" : "hide"} alt={"Cover of tinker tim"} />

            <h1 className={secondHeaderOpacity ? "secondHeaderShow" : "secondHeaderHide"}> The Authors</h1>

            <p className={secondTextOpacity ? "secondTextShow" : "secondTextHide"}> We're a group of four students at the University of Washington. Darren and Alex will be graduating this year, Kenny will be pursuing his Masters, and Aroosh will be entering his senior year. </p>

        </>
    )
}

export default Reason;