import React, { useEffect, useState } from "react";

import { BiMenuAltRight } from "react-icons/bi"
import { AiOutlineClose } from "react-icons/ai"

import { Link, useHistory } from "react-router-dom"

import classes from "./Header.module.scss"

const Header = () => {
    const [menuOpen, setMenuOpen] = useState(false)
    const history = useHistory();
    const menuToggleHandler = () => {
        setMenuOpen((p) => !p);
    }

    const [size, setSize] = useState({
        width: undefined,
        height: undefined,
    });

    useEffect(() => {
        if (size.width > 768 && menuOpen) {
            setMenuOpen(false);
        }
    }, [size.width, menuOpen]);

    useEffect(() => {
        const handleResize = () => {
            setSize({
                width: window.innerWidth,
                height: window.innerHeight,
            });
        };
        window.addEventListener("resize", handleResize);

        return () => window.removeEventListener("resize", handleResize);
    }, []);

    const orderClickHandler = () => {
        menuToggleHandler();
        history.push("/order");
    }

    return (<header className={classes.header}> 
        <div className = {classes.header__content}>
            <Link to ="/" className = {classes.header__content__logo}> 
                Tinker Tim
            </Link>
            <nav className = {`${classes.header__content__nav} ${menuOpen && size.width < 768 ? classes.isMenu: ""} `}>
                <ul>
                    <li>
                        <Link to="/" onClick = {menuToggleHandler}> The Story </Link>
                    </li>
                    <li>
                        <Link to ="/reason" onClick = {menuToggleHandler}> The Reason </Link>
                    </li>
                    <li>
                        <Link to ="/presales" onClick = {menuToggleHandler}> Pre-sales </Link>
                    </li>
                    <li>
                        <Link to ="/contact" onClick = {menuToggleHandler}> Contact Us </Link>
                    </li>

                </ul>
                <button onClick = {orderClickHandler} > Order </button>
            </nav>
        
            <div className = {classes.header__content__toggle}> 
                {!menuOpen ? ( <BiMenuAltRight onClick = {menuToggleHandler} /> ): 
                (<AiOutlineClose onClick = {menuToggleHandler} /> )}
            </div>
        </div>
    </header>
    );
}

export default Header;